<template>
  <div class="search">
    <van-nav-bar
      left-text="返回"
      right-text="首页"
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />
    <van-search
      v-model="value"
      placeholder="请输入搜索关键词"
      @search="onSearch"
      class="search"
    />

    <van-card
      v-for="item in list"
      :key="item.id"
      :num="item.amount"
      :price="item.price.toFixed(2)"
      desc="描述信息"
      :title="item.name"
      :thumb="item.coverImage | dalImg"
      @click-thumb="toDetail(item)"
      class="lt"
    />
  </div>
</template>

<script>
import { Toast } from "vant";
import { loadProductAPI } from "../services/products";
export default {
  data() {
    return {
      value: "",
      page: 1,
      per: 9999,
      list: [],
    };
  },
  created() {
    this.onSearch();
  },
  methods: {
    toDetail(item) {
      this.$router.push({
        name: "detail",
        params: {
          id: item.id,
        },
      });
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    onClickRight() {
      this.$router.push({
        name: "home",
      });
    },

    //搜索
    async onSearch() {
      const res = await loadProductAPI(this.page, this.per);

      this.list = res.data.filter((v) =>
        v.name.includes(this.value ? this.value : this.$route.params.content)
      );
      if (this.list == "") {
        Toast.success("暂无商品");
      }

    },
  },
};
</script>

<style scoped>
.lt {
  overflow: auto;
}
</style>
